import { Button } from "../Button/Button";
import { Icon } from "../Button/SvgIcon/Icon";
import { SvgIcon } from "../Button/SvgIcon/SvgIcon";
import "./ModalWindow.scss";

export type ModalProps = {
  hidden?: boolean;
  onClick: () => void;
  onClose: () => void;
};

export const ModalWindow = (props: ModalProps) => {
  return props.hidden ? (
    <></>
  ) : (
    <div className="modal-window" onClick={props.onClose}>
      <div className="modal-conteiner">
        <div className="text">
          <p>    ?</p>
          <p>?</p>
        </div>
        <div>
          <SvgIcon icon={Icon.Pingvin} />
        </div>
        <div className="button-conteiner">
          <Button text="" onClick={props.onClick} />
          <Button text="" onClick={props.onClose} />
        </div>
      </div>
    </div>
  );
};
