import moment from "moment";
import "moment/locale/ru";
import "./Header.scss";

export const Header = () => {
  const today = moment();
  return (
    <div className="header">
        <h1> #132</h1>
      <div className="caption">      
        <p>: , . , .3, .1</p>
      <p>: {today.format("dddd, MM.DD.YYYY .")}</p>
      </div>
    </div>
  );
};
